import React from "react";

import AOS from 'aos';
import 'aos/dist/aos.css';

const InfoRowRight = (props) => {
    
    AOS.init();
    return(
        <div className="row" data-aos="fade-right" data-aos-offset="300">
            <div className="col-lg-6 col-sm-6 vertical-center">
                <div className="img-section">

                    {props.infoImgR === undefined ?
                       <></>
                    :
                        <img src={props.infoImgR} alt="MDS" />
                    }
                    {props.infoVideoR === undefined ?
                       <></>
                    :
                        <div className="contentVideo">
                            <iframe 
                                src={props.infoVideoR}
                                width="640" 
                                height="360" 
                                frameborder="0" 
                                allow="autoplay; fullscreen; picture-in-picture" 
                                allowfullscreen>
                            </iframe>
                        </div>
                    }
                    
                    

                </div>
            </div>

            <div className="col-lg-6 col-sm-6 vertical-center">
                <div className="contentInfo">
                    <h1 className="section-titles int">{props.infoTitleR}</h1>
                    <p dangerouslySetInnerHTML={{__html: props.infoTextR}}></p>
                </div>
            </div>
        </div>
    );
        
}

export default InfoRowRight;